<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10 zd-cm-res-fl"
              custom-icon="zd-common-icon-add" @click="toAddOrder">
        入库单
      </Button>
      <div class="zd-common-fr zd-cm-res-fr">
        <DatePicker :editable="false" type="daterange" :options="dateOptions" placeholder="日期"
                    class="zd-cm-w220 zd-common-input-h38 zd-common-fl zd-common-mr10" @on-change="changeDate"></DatePicker>
        <Select clearable v-model="type" placeholder="选择入库类型" class="zd-common-w150 zd-common-mr10 zd-common-input-h38 zd-common-fl">
          <Option v-for="item in typeArr" :value="item.code" :key="item.code">{{ item.name }}</Option>
        </Select>
        <Select clearable v-model="warehouse" placeholder="选择入库仓库"
                class="zd-common-w200 zd-common-mr10 zd-common-input-h38 zd-common-fl">
          <Option v-for="item in warehouseArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <Select clearable v-model="supplier" placeholder="全部供应商"
                class="zd-common-w200 zd-common-mr10 zd-common-input-h38 zd-common-fl">
          <Option v-for="item in supplierArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <Input v-model="orderNO" :maxlength="50" placeholder="入库单号" class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>
    </div>
    <!--查看-->
    <Modal v-model="viewModal" :mask-closable="false" :width="1100" ref="print">
      <h3 slot="header" class="zd-common-modal-header">
        <span>查看入库单</span>
      </h3>
      <div class="zd-common-clearFix">
        <span class="zd-common-fl zd-common-mr14">入库单号：<span class="zd-common-font-weight">{{ allDetail.num }}</span></span>
        <span class="zd-common-fl zd-common-mr14">入库日期：<span class="zd-common-font-weight">{{ allDetail.date }}</span> </span>
        <span class="zd-common-fl zd-common-mr14">入库仓库：<span class="zd-common-font-weight">{{ allDetail.warehouseName }}</span> </span>
        <span class="zd-common-fl zd-common-mr14">入库类型：<span class="zd-common-font-weight">{{ allDetail.kindCode }}</span> </span>
        <span class="zd-common-fl zd-common-mr14">供应商：<span class="zd-common-font-weight">{{ allDetail.goodsSupplierName }}</span></span>
        <span class="zd-common-fl zd-common-mr14">采购人：<span class="zd-common-font-weight">{{ allDetail.buyserName }}</span></span>
      </div>
      <div class="zd-common-h300 zd-common-mt10">
        <vue-scroll>
          <Table border :columns="goodsCols" :data="goodsData" :summary-method="handleSummary" show-summary></Table>
        </vue-scroll>
      </div>
      <div class="zd-common-mt10 zd-common-clearFix">
          <span class="zd-common-fl">制单人：{{ allDetail.userName }}</span><span class="zd-common-fl zd-common-ml20">制单日期：{{ allDetail.productDate }}</span>
      </div>
      <div class="zd-common-mt10 zd-common-clearFix">
        <span class="zd-common-clearFix"><span class="zd-common-fl">备注：</span><span class="zd-common-fl zd-cm-remark">{{ allDetail.note }}</span></span>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8"  @click="printHandle">打印</Button>
        <Button @click="viewModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--删除-->
    <Modal v-model="delModal" :mask-closable="false" :width="590">
      <h3 slot="header" class="zd-common-modal-header">
        <span>删除入库单</span>
      </h3>
      <Alert type="warning" show-icon class="zd-common-warning-c">删除入库单将同步删除该入库单中所有已入库物品，导致数据统计错误，请谨慎操作！</Alert>
      <Form ref="deleteForm" :model="deleteForm">
        <FormItem label="删除原因：" class="zd-common-mb0">
          <Input class="zd-common-fl" v-model.trim="deleteForm.reason" placeholder="" :maxlength="100" type="textarea" :autosize="{minRows: 3,maxRows: 3}"></Input>
        </FormItem>
      </Form>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="handleSubmit" :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="delModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import { utils } from '@comm/script/utils/index'
  export default {
    data() {
      return {
        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '入库单号',
            align: 'center',
            key: 'putInOrder'
          },
          {
            title: '入库日期',
            align: 'center',
            key: 'putInDate',
          },
          {
            title: '入库类型',
            align: 'center',
            key: 'putInType',
          },
          {
            title: '入库仓库',
            align: 'center',
            key: 'warehouse',
          },
          {
            title: '供应商',
            align: 'center',
            key: 'supplier',
          },
          {
            title: '物品数量',
            align: 'center',
            key: 'number'
          },
          {
            title: '制单人',
            align: 'center',
            key: 'orderPerson'
          },
          {
            title: '制单日期',
            align: 'center',
            key: 'orderDate'
          },
          {
            title: '操作',
            align: 'center',
            key: 'action',
            width:260,
            render: (h, params) => {
              if(params.row.canEdit){
                return h('div', [
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px',
                      marginRight:'10px'
                    },
                    on: {
                      click: () => {
                        this.showViewModal(params.row.id);
                      }
                    }
                  }, '查看'),
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px',
                      marginRight:'10px'
                    },
                    on: {
                      click: () => {
                          this.showModifyModal(params.row.id);
                      }
                    }
                  }, '修改'),
                  h('Button', {
                    props:{
                      type:'error',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px'
                    },
                    on: {
                      click: () => {
                        this.showDelModal(params.row.id);
                      }
                    }
                  }, '删除'),
                ]);
              }else {
                return h('div', [
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px',
                      marginRight:'10px'
                    },
                    on: {
                      click: () => {
                        this.showViewModal(params.row.id);
                      }
                    }
                  }, '查看')
                ]);
              }
            }
          }
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,
        //入库单查看
        saveLoading:false,
        goodsCols:[
          {
            type: 'index',
            title:'序号',
            align: 'center',
            width:65
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code',
          },
          {
            title: '物品分类',
            align: 'center',
            key: 'classify',
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',
            width:65
          },
          {
            title: '入库数量',
            align: 'center',
            key: 'number',
          },
          {
            title: '价格',
            align: 'center',
            key: 'price'
          },
          /*{
            title: '购买人',
            align: 'center',
            key: 'purchaser',
          },*/
          {
            title: '金额',
            align: 'center',
            key: 'amount',
          }
        ],
        goodsData:[],
        viewModal:false,
        allDetail:{},
        //删除
        delModal:false,
        deleteForm:{
          reason:''
        },
        delId:'',
        //查询
        dateOptions: this.dateRange,
        startDate:'',
        endDate:'',
        type:'',
        typeArr:[],
        supplier:'',
        supplierArr:[],
        warehouse:'',
        warehouseArr:[],
        orderNO:'',
        selectLoading:false,
      }
    },
    created(){
      this.getIntoKindList().then((response)=>{
        this.typeArr = response.data
      });
      this.getGoodsSupplierAllEnable().then((response)=>{
        this.supplierArr = response.data;
      });
      this.getChargedStoreRoom().then((response)=>{
        this.warehouseArr = response.data
      });
      this.getIntoWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.supplier,this.orderNO);
    },
    methods:{
      //打印
      printHandle(){
        // window.print();
        this.$print(this.$refs.print)
      },
      //日期
      changeDate(arr){
        this.startDate = arr[0];
        this.endDate = arr[1];
      },
      //合计
      handleSummary ({ columns, data }) {
        const sums = {};
        columns.forEach((column, index) => {
          const key = column.key;
          if (index === 0) {
            sums[key] = {
              key,
              value: '合计'
            };
            return;
          }
          const values = data.map(item => Number(item[key]));
          if (!values.every(value => isNaN(value)) && (index===7 || index===9)) {
            const v = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[key] = {
              key,
              value: v
            };
          } else {
            sums[key] = {
              key,
              value: ''
            };
          }
        });
        return sums;
      },
      //添加入库单
      toAddOrder(){
        this.$router.push({path:'/addPutInOrder'});
      },
      //查看
      showViewModal(id){
        this.viewModal = true;
        this.getIntoWarehouseRecordInfo(id);
      },
      getIntoWarehouseRecordInfo(id) {
        this.goodsData = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getIntoWarehouseRecordInfo.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            this.allDetail = aData;
            aData.goodsList.map((item)=>{
              this.goodsData.push(
                {
                  name:item.name,
                  code:item.code,
                  classify:item.goodsKind,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  number:item.count,
                  price:item.unitPrice,
                  amount:item.count * parseFloat(item.unitPrice)
                }
              )
            })

          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //修改
      showModifyModal(id){
        this.$router.push({ path: `/editPutInOrder/${id}` });
      },
      //删除
      showDelModal(id){
        this.delModal = true;
        this.delId = id;
        this.deleteForm.reason = '';
      },
      deleteIntoWarehouseRecord(id,deleteCause) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!deleteIntoWarehouseRecord.action',
          data:{
            id:id,
            deleteCause:deleteCause
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData!==""){
              this.postWarningTip(aData);
            }
            this.saveLoading = false;
            this.getIntoWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.supplier,this.orderNO);
          })
          .catch((error) => {
            this.saveLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      handleSubmit() {
        this.saveLoading=true;
        this.delModal = false;
        this.deleteIntoWarehouseRecord(this.delId,this.deleteForm.reason);
      },
      //列表
      getIntoWarehouseRecordPage(pageNum,pageSize,startDate,endDate,kindCode,storeroomId,goodsSupplierId,num) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getIntoWarehouseRecordPage.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            startDate:startDate,
            endDate:endDate,
            kindCode:kindCode,
            storeroomId:storeroomId,
            goodsSupplierId:goodsSupplierId,
            num:num
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push(
                  {
                    id:item.id,
                    putInOrder:item.num,
                    putInDate:item.date,
                    putInType:item.kind,
                    warehouse:item.warehouseName,
                    supplier:item.goodsSupplierName,
                    number:item.goodsNum,
                    orderPerson:item.user,
                    orderDate:item.productDate,
                    canEdit:item.canEdit
                  }
                )
              })
            }
            /*if(this.pageNum>aData.totalPageCount){
              this.pageNum = aData.totalPageCount;
              this.getIntoWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.supplier,this.orderNO);
            }*/
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.getIntoWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.supplier,this.orderNO);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.getIntoWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.supplier,this.orderNO);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.getIntoWarehouseRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.warehouse,this.supplier,this.orderNO);
      },
    },
    mounted(){}
  }
</script>
<style lang="less" scoped>
  .ivu-table .demo-table-info-row td{
    background-color: rgba(28, 144, 243, 0.1);
  }
  /*适配*/
  @media screen and (max-width: 1420px){
    .zd-cm-res-fr{
      float: left !important;
    }
    .zd-cm-res-fr > *,.zd-cm-res-fl{
      margin-bottom:10px;
    }
  }
</style>



